#quote-box {
  width: 40vw;
  height: 50vh;
  text-align: center;
  margin: 0 auto;
  margin-top: calc(40vh / 2);
  background: #8a2be2;
  color: #fff;
  padding: 20px;
  border-radius: 10px;
}
#quote-container {
  width: 100%;
  height: 40%;
}
#text {
  margin-top: 20px;
  margin-bottom: 20px;
}

#author {
  display: block;
  text-align: right;
  margin-bottom: 20px;
}
#new-quote {
  margin: 0 auto;
  display: block;
  padding: 10px;
  border-radius: 5px;
  border: 2px solid #fff;
  background: #8a2be2;
  color: #fff;
}

#tweet-quote {
  display: block;
  position: relative;
  left: 0;
  top: 50px;
}
